.detail {
    .in--section--detail {
        position: fixed;
        z-index: 1;
        box-sizing: border-box;
        width: 100%;
        padding: 0 1rem;

        @media (min-width: $tablet) {
            & {
                width: 100%;
                padding: 0 1.8rem;
            }
        }

        @media (min-width: $desktop) {
            & {
                width: 1100px;
                padding: 0 4rem;
            }
        }
    }

    .container {
        width: 100%;
        margin: 0 auto;

        padding: {
            top: 4.5rem;
            right: 1rem;
            bottom: 1.5rem;
            left: 1rem;
        }

        box-sizing: border-box;

        .title {
            font: {
                size: 1.3rem;
                weight: bold;
            }

            line-height: 1.5;
            color: rgb(92, 92, 92);
        }

        .profile {
            padding: 1.5rem 0;
            color: rgb(92, 92, 92);
            // font-size: 1rem;
            font-size: 0.85rem;
            display: flex;
            justify-content: space-between;
            align-items: center;

            .info {
                display: flex;
                align-items: center;

                .logo {
                    margin-right: 16px;

                    img {
                        width: 36px;
                        margin-top: 3px;
                    }
                }

                .author {
                    line-height: 1.5;
                }
            }

            a {
                height: 33px;
                line-height: 33px;
                font-size: 0.85rem;
                padding: 0 14px;
                color: rgb(86, 86, 86);
                background-color: #fff;
                border: 1px solid rgb(103, 103, 103, );
                border-radius: 4px;
                cursor: pointer;
            }
        }

        .description {
            color: rgb(91, 91, 91);
            line-height: 1.8;

            .image-box {
                width: 100%;
                text-align: center;

                img {
                    // width: auto !important;
                    height: auto !important;
                    max-width: 100% !important;
                    // max-height: 500px !important;
                    vertical-align: top;
                    margin-bottom: 12px;
                    object-fit: contain;
                }
            }

            .youtube-wrap {
                position: relative;
                width: 100%;
                padding-bottom: 56.25%;

                iframe {
                    position: absolute;
                    width: 100%;
                    height: 100%;
                }
            }

            p,
            div {
                margin: 0;
            }

            h1,
            h2,
            h3,
            h4,
            h5,
            h6 {
                font-weight: bold;
                margin: 0;
                margin-bottom: 12px;
            }

            h1 {
                font-size: 24px;
            }

            h2 {
                font-size: 20px;
            }

            h3 {
                font-size: 18px;
            }

            strong,
            b {
                font-weight: bold;
            }

            blockquote {
                padding-left: 20px;
                padding-right: 8px;
                border-left: 5px solid #ccc;
            }

            ul,
            ol {
                list-style: initial;
                display: block;
                margin-block-start: 1rem;
                margin-block-end: 1rem;
                margin-inline-start: 0px;
                margin-inline-end: 0px;
                padding-inline-start: 20px;
            }

            a {
                overflow: hidden;
                word-break: break-all;
                // color: rgb(120, 155, 221);
                text-decoration: underline !important;
            }

            em {
                font-style: italic;
            }
        }

        .buttons {
            position: relative;
            padding-top: 2rem;

            .alert {
                position: absolute;
                height: 33px;
                line-height: 33px;
                background-color: rgb(255, 255, 255);
                opacity: 0;
                transform: translateX(-100%);
                transition: 0.4s ease-in-out;

                &.on {
                    opacity: 1;
                    transform: none;
                }
            }

            button {
                height: 33px;
                line-height: 33px;
                font-size: 0.85rem;
                padding: 0 14px;
                color: rgb(86, 86, 86);
                background-color: #fff;
                border: 1px solid rgb(103, 103, 103);
                border-radius: 4px;
                cursor: pointer;
            }

            .share {}

            .list-view {
                float: right;
            }
        }
    }

    @media (min-width: $mobile) {
        & .container {
            width: 80%;

            padding: {
                top: 5rem;
                right: 0;
                bottom: 3rem;
                left: 0;
            }

            .profile {
                padding: 2.5rem 0;
            }

            .description {
                ul {
                    padding-left: 30px;
                }
            }

            .buttons {
                padding-top: 4rem;

                .alert {
                    height: 37px;
                    line-height: 37px;
                }

                button {
                    height: 37px;
                    line-height: 37px;
                    padding: 0 16px;
                }
            }
        }
    }

    @media (min-width: $desktop) {
        & .container {
            width: 66.666667%;

            padding: {
                top: 5.5rem;
                right: 0;
                bottom: 4rem;
                left: 0;
            }

            .title {
                font: {
                    size: 2rem;
                }
            }

            .profile {
                padding: 3rem 0;
                font-size: 1rem;

                .info {
                    .logo {
                        img {
                            width: 40px;
                        }
                    }

                    .author {
                        line-height: 1.3;

                        .name {}
                    }
                }
            }

            .description {
                ul {
                    padding-left: 40px;
                }
            }
        }
    }
}